import * as is from './is';
import * as TOKEN from './TOKEN';
import Layer, { protect } from './Layer';
import Url, { newUrl } from './Url';

export default class NamespaceRule extends Layer       {
	
	get [Symbol.toStringTag] () { return 'NamespaceRule'; }
	
	                 keywordLiteral        ;
	        prefixLiteral         = '';
	urlLayer      ;
	get block () { return !this.urlLayer; }
	
	constructor (keywordLiteral         ) {
		super();
		this.keywordLiteral = keywordLiteral || 'namespace';
		return this;
	}
	
	[TOKEN.consume] (                   ) {
		switch ( TOKEN.type ) {
			case TOKEN.whitespace:
				return this;
			case TOKEN.string:
				if ( this.urlLayer ) { break; }
				( this.urlLayer = new Url(null) ).valueLiteral = TOKEN.literal;
				return this;
			case TOKEN.url:
				if ( this.urlLayer ) { break; }
				if ( TOKEN.literal[3]==='(' ) { return newUrl(this, 'urlLayer'); }
				break;
			case TOKEN.function_:
				if ( is.url_ (TOKEN.literal) ) {
					if ( this.urlLayer ) { break; }
					return this.urlLayer = new Url(TOKEN.literal.slice(0, 3));
				}
				break;
			case TOKEN.ident:
				if ( this.prefixLiteral || this.urlLayer ) { break; }
				this.prefixLiteral = TOKEN.literal;
				return this;
			case ';':
				return this[TOKEN.parent];
			case TOKEN.comment:
				return this;
		}
		return null;
	}
	
	[Symbol.toPrimitive] (                   ) {
		const urlLiteral = '' + this.urlLayer;
		return `@${this.keywordLiteral}${this.prefixLiteral && ' ' + this.prefixLiteral}${urlLiteral && ( urlLiteral[0]==='"' || urlLiteral==='\'' ) ? '' : ' '}${urlLiteral};`;
	}
	
	* [Symbol.iterator] (                   )                                {
		yield `@${this.keywordLiteral}${this.prefixLiteral && ' ' + this.prefixLiteral} ${this.urlLayer};`;
	}
	
	static default = protect(this);
	
};
