import TypeError from '.TypeError';

import * as TOKEN from './TOKEN';
import Layer, { protect } from './Layer';

export default class ClassSelector extends Layer       {
	
	         itemLiteral        ;
	
	constructor (itemLiteral        ) {
		//if ( !itemLiteral ) { throw SyntaxError(`Class Selector 不能为 .`); }
		super();
		TOKEN.currentLayer && ( TOKEN.collection_class[TOKEN.collection_class.length] = this );
		this.itemLiteral = itemLiteral;
		return this;
	}
	
	[TOKEN.consume] (                   )        { throw TypeError(`classSelector[consume]`); }
	
	[Symbol.toPrimitive] (                   ) { return `.${this.itemLiteral}`; }
	
	* [Symbol.iterator] (                   ) { throw TypeError(`beautify classSelector`); }
	
	static default = protect(this);
	
};
