import TypeError from '.TypeError';

import * as TOKEN from './TOKEN';
import Layer, { protect } from './Layer';

export default class IdSelector extends Layer       {
	
	valueLiteral        ;
	
	constructor (valueLiteral        ) {
		//if ( !valueLiteral ) { throw SyntaxError(`ID Selector 不能为 #`); }
		super();
		TOKEN.currentLayer && ( TOKEN.collection_id[TOKEN.collection_id.length] = this );
		this.valueLiteral = valueLiteral;
		return this;
	}
	
	[TOKEN.consume] (                )        { throw TypeError(`idSelector[consume]`); }
	
	[Symbol.toPrimitive] (                ) { return `#${this.valueLiteral}`; }
	
	* [Symbol.iterator] (                ) { throw TypeError(`beautify idSelector`); }
	
	static default = protect(this);
	
};
